import { Tensor, TensorType } from "../tensor";
export class AiTextGeneration {
    constructor(inputs, modelSettings) {
        this.schema = {
            input: {
                type: "object",
                oneOf: [
                    {
                        properties: {
                            prompt: {
                                type: "string",
                            },
                            stream: {
                                type: "boolean",
                                default: false,
                            },
                        },
                        required: ["prompt"],
                    },
                    {
                        properties: {
                            messages: {
                                type: "array",
                                items: {
                                    type: "object",
                                    properties: {
                                        role: {
                                            type: "string",
                                        },
                                        content: {
                                            type: "string",
                                        },
                                    },
                                    required: ["role", "content"],
                                },
                            },
                        },
                        required: ["messages"],
                    },
                ],
            },
            output: {
                oneOf: [
                    {
                        type: "object",
                        properties: {
                            response: {
                                type: "string",
                            },
                        },
                    },
                    {
                        type: "string",
                        format: "binary",
                    },
                ],
            },
        };
        this.inputs = inputs;
        this.modelSettings = modelSettings;
    }
    preProcessing() {
        let prompt = "";
        if (this.inputs.messages === undefined) {
            prompt = this.inputs.prompt;
        }
        else {
            for (let i = 0; i < this.inputs.messages.length; i++) {
                const inp = this.inputs.messages[i];
                switch (inp.role) {
                    case "system":
                        prompt +=
                            this.modelSettings.preProcessingArgs.startSysPrompt +
                                inp.content +
                                this.modelSettings.preProcessingArgs.endSysPrompt +
                                "\n";
                        break;
                    case "user":
                        prompt += "[INST]" + inp.content + "[/INST]\n";
                        break;
                    case "assistant":
                        prompt += inp.content + "\n";
                        break;
                    default:
                        throw new Error("Invalid role: " + inp.role);
                }
            }
        }
        this.preProcessedInputs = prompt;
    }
    generateTensors() {
        this.tensors = [
            new Tensor(TensorType.String, [this.preProcessedInputs], {
                shape: [1],
                name: "INPUT_0",
            }),
            new Tensor(TensorType.Uint32, [256], {
                shape: [1],
                name: "INPUT_1",
            }),
        ];
    }
    postProcessing(response) {
        this.postProcessedOutputs = { response: response.name.value[0] };
    }
    postProcessingStream(response) {
        return { response: response.name.value[0] };
    }
}
