import { Tensor, TensorType } from "../tensor";
export class AiSpeechRecognition {
    constructor(inputs, modelSettings) {
        this.schema = {
            input: {
                type: "object",
                properties: {
                    audio: {
                        oneOf: [
                            { type: "string", format: "binary" },
                            {
                                type: "array",
                                items: {
                                    type: "number",
                                },
                            },
                        ],
                    },
                },
                required: ["audio"],
            },
            output: {
                type: "object",
                properties: {
                    text: {
                        type: "string",
                    },
                },
            },
        };
        this.inputs = inputs;
        this.modelSettings = modelSettings;
    }
    preProcessing() {
        this.preProcessedInputs = this.inputs;
    }
    generateTensors() {
        this.tensors = [
            new Tensor(TensorType.Uint8, this.preProcessedInputs.audio, {
                shape: [1, this.preProcessedInputs.audio.length],
                name: "audio",
            }),
        ];
    }
    postProcessing(response) {
        this.postProcessedOutputs = { text: response.name.value[0] };
    }
}
